@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 1em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

a {
  text-decoration: none;
  display: inline;
  font-weight: 700;
  color: currentColor;
}

.App {
  height: 100vh;
  background-size: cover;
  color: #fff;
  text-align: center;
}
.App > * {
  display: flex;
  align-items: center;
  justify-content: center;
}

.GeoLocation { height: 10%; justify-content: space-evenly; }

.WeatherForcast {
  height: 80%;
  background-color: rgba(0, 0, 0, 0.6);
  flex-direction: column;
  font-size: 1.2em;
}

.WeatherIcon {
  width: 200px;
}
.Weather {
  font-size: 1.8em;
  font-weight: 700;
}
.Location {
  font-size: 1.5em;
}
.AvgTemprature {
  font-size: 2em;
  font-weight: 700;
}
.HumPre, .Wind {
  font-size: 0.8em;
  padding: 5px;
}
.Error {
  font-weight: 700;
}

.Footer { height: 10%; flex-direction: column; }

input, select {
  width: 65vw;
  border: none;
  outline: none;
  padding: 10px;
  font-size: 1em;
  background-color: rgba(0, 0, 0, 0.6);
  color: rgba(255, 255, 255, 0.8);
}

select {
  width: 30vw;
}

option {
  background-color: rgba(0, 0, 0, 0.8);
}

